import React, { Component } from 'react';
import $ from 'jquery';

class Ajax extends Component {
  constructor(){
    super()
    this.state={
     arr:[]
    }
  }
  componentDidMount (){
   $.ajax({
    url:'http://localhost:8005/aaa',
    type:'get',
    success:function(a){
      console.log(a)
      this.setState({arr:a});
    }.bind(this)
   })
  }
  render (){
    return (
      <div>
         <ul>{this.state.arr.map(function(e){
                       return <li key={e.uid}>{e.uid}{e.news_title}</li>
                        })}</ul>
      </div>
      )
  }
}

export default Ajax


